import * as React from "react";
import './Header.css'
export const Header=()=>{

    return(
        <header>
            <div className="header__wrapper">
                <h1>Online Zoo</h1>
                <div className="header__logo" style={{
                    backgroundImage:'url(./public/assets/svg/logo.svg)'}}>
                    <a href="./index.html">
                    </a>
                </div>
                <nav className="header__nav nav">
                    <ul>
                        <li className="nav-item_active"><a href="./index.html">About</a></li>
                        <li className="nav-item"><a href="pages/zoos/panda.html">Zoos</a></li>
                        <li className="nav-item"><a href="pages/intermediatePage/intermediatePage.html">Map</a></li>
                        <li className="nav-item"><a href="#form-contact">Contact us</a></li>
                        <li className="nav-item">
                            <a href="https://www.figma.com/file/HKt5Nlx0jghQtJp6jW9q8F/zooApp">Design</a></li>
                    </ul>
                </nav>
                <div className="header__switch-burger">
                    <div className="header__switch switch">
                        <input type="checkbox" id="switch-checkbox"/>
                    </div>
                    <div className="header__burger-menu">
                        <input id="header__burger-menu__toggle" type="checkbox"/>
                        <label className="header__burger-menu__btn" htmlFor="header__burger-menu__toggle">
                            <span></span>
                        </label>
                        <ul className="header__burger-menu-nav">
                            <li><a className='header__burger-menu__item' href="./index.html">About</a></li>
                            <li><a className='header__burger-menu__item' href="pages/zoos/panda.html">Zoos</a></li>
                            <li><a className='header__burger-menu__item'
                                   href="pages/intermediatePage/intermediatePage.html">Map</a></li>
                            <li><a className='header__burger-menu__item' href="#form-contact">Contact us</a></li>
                            <li><a className='header__burger-menu__item'
                                   href="https://www.figma.com/file/HKt5Nlx0jghQtJp6jW9q8F/zooApp">Design</a></li>
                        </ul>
                    </div>
                </div>

            </div>
        </header>
    )
}
